@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300&display=swap");

@layer base {
  body {
    @apply font-[Raleway];
    @apply bg-[#0a192f];
    @apply text-gray-300;
  }
  li {
    @apply px-4;
    @apply cursor-pointer;
  }
}

.content-div {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 250px;
  cursor: pointer;
}

.content-div:hover > .gradient {
  background-image: linear-gradient(
    to right,
    rgba(112, 157, 255, 0.7),
    hsla(242, 74%, 81%, 0.5)
  ) !important;
}

.navbar > li {
  display: inline-block;
}
.navbar > li:hover {
  text-decoration: underline;
  text-underline-offset: 10px;
  text-decoration-thickness: 3px;
  -moz-text-decoration-color: #008037;
  text-decoration-color: #008037;
}

li::marker {
  content: "" !important;
}


